<template>
  <div class="class">
    <tagSelect
      :ref="`tagSelect${index}`"
      :dataSource="item"
      v-for="(item,index) in tags"
      :key="index"
      @tagClick="tagClick(index, arguments)"
    ></tagSelect>
    <list :dataList="vodrows"></list>
    <infinite-loading :identifier="identifier" v-if="dataSource.action" @infinite="infinite" ref="infiniteLoading"></infinite-loading>
  </div>
</template>

<script>
import config from "../js/config.js";
import list from "../components/list.vue";
import tagSelect from "../components/tagSelect.vue";
export default {
  components: {
    list,
    tagSelect
  },
  data() {
    return {
      page: 0,
      identifier: 0,
      vodrows: [],
      dataSource: {
        params: {},
        action: ""
      },
      classList: [
        {
          key: "categories",
          query: "cateid",
          id: 0
        },
        {
          key: "areas",
          query: "areaid",
          id: 0
        },
        {
          key: "years",
          query: "yearid",
          id: 0
        },
        {
          key: "definitions",
          query: "definition",
          id: 0
        },
        {
          key: "durations",
          query: "duration",
          id: 0
        },
        {
          key: "freetypes",
          query: "freetype",
          id: 0
        },
        {
          key: "mosaics",
          query: "mosaic",
          id: 0
        },
        {
          key: "langvoices",
          query: "langvoice",
          id: 0
        },
        {
          key: "orders",
          query: "order",
          id: 0
        }
      ]
    };
  },
  computed: {
    tags() {
      let tags = [];
      this.classList.forEach(res => {
        let arr = this.dataSource[res.key];
        if (arr) {
          tags.push({
            items: [
              {
                id: 0,
                title: "全部"
              }
            ].concat(
              arr.map(res2 => {
                let id =
                  res2.keyid || res2.cateid || res2.areaid || res2.yearid;
                return {
                  ...res2,
                  id
                };
              })
            ),
            id: res.id
          });
        }
      });
      return tags;
    }
  },
  created() {
    this.loadBaseData();
  },
  methods: {
    tagClick(index, args) {
      let idx = 0;
      if (args) {
        idx = args[0];
      } else {
        idx = this.$refs["tagSelect" + index][0].idx;
      }
      this.classList[index].id = this.tags[index].items[idx].id;
    },
    itemClick(item) {
      if (item.spid) {
        uni.navigateTo({
          url: "./detail?spid=" + item.spid
        });
      }
    },
    infinite($state) {
      console.log("onReachBottom");
      this.loadPageData(this.page + 1, $state);
    },
    loadBaseData() {
      this.$axios
        .request({
          url: config.origin + "/vod/recommend",
          params: config.query()
        })
        .then(res => {
          console.log("success", res);
          if (res.status == 200 && res.data.retcode == 0) {
            this.dataSource = res.data.data;
            // this.loadPageData(this.page);
          } else {
            weui.topTips(res.data.errmsg || res.statusCode || "fail");
          }
        })
        .catch(res => {
          weui.topTips(res.message || "fail");
        });
    },
    loadPageData(page, $state) {
      this.$axios
        .request({
          url:
            config.origin +
            "/vod/listing-" +
            this.classList
              .map(item => {
                return item.id;
              })
              .join("-") +
            "-" +
            this.page,
          params: config.query()
        })
        .then(res => {
          console.log("success", res);
          if (res.status == 200 && res.data.retcode == 0) {
            this.page = page;
            if (this.page == 1) {
              this.vodrows = res.data.data.vodrows;
            } else {
              this.vodrows = this.vodrows.concat(res.data.data.vodrows);
            }
            if (res.data.data.vodrows.length < 16) {
              $state && $state.complete();
            } else {
              $state && $state.loaded();
            }
          } else {
            weui.topTips(res.data.errmsg || res.statusCode || "fail");
            $state && $state.loaded();
          }
        })
        .catch(res => {
          if ($state) {
            $state.loaded();
          }
          weui.topTips(res.message || "fail");
        });
    }
  },
  watch: {
    classList: {
      deep: true,
      handler(classList) {
        console.log("classList", classList);
        this.page = 0;
        this.vodrows = [];
        this.identifier = new Date().valueOf()
        // this.loadPageData(this.page);
      }
    }
  }
};
</script>

