.special {
  .header {
    width: 100vw;
    overflow-x: scroll;
    display: flex;

    &::-webkit-scrollbar {
      display: none;
    }

    .item {
      margin: 2px 5px;

      .image {
        width: 60px;
        height: 60px;
        border-radius: 30px;
      }

      .title {
        text-align: center;
        font-size: 12px;
      }
    }
  }
}
</style>
