import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
Vue.use(ElementUI);
Vue.prototype.$util = {
  _D: (time) => {
    return require('dayjs')(time).format('YYYY-MM-DD HH:mm:ss')
  },
  _N: (time, num = 0) => {
    return Math.floor(time * Math.pow(10, num)) / Math.pow(10, num)
  },
  Log: () => {
    console.log(Vue.prototype.$util._D(), ...arguments)
  },
  huobiParams(data = {}, url = "", method = 'get') {
    let Timestamp = require("dayjs")(
      new Date().getTime() - 8 * 60 * 60 * 1000
    ).format("YYYY-MM-DDTHH:mm:ss");
    let accessKey = store.state.accessKey
    let secretKey = store.state.secretKey
    let params = {
      Timestamp: Timestamp,
      AccessKeyId: accessKey,
      SignatureMethod: "HmacSHA256",
      SignatureVersion: "2",
      ...data
    };
    let str1 = `${method.toUpperCase()}\napi.huobi.pro\n${url.replaceAll('https://api.huobi.pro', '')}\n`;
    let str2 = "";
    let keys = Object.keys(params).sort()
    for (const index in keys) {
      let key = keys[index]
      str2 += `${str2 ? "&" : ""}${key}=${encodeURIComponent(params[key])}`;
    }
    let str3 = str1 + str2;
    console.log(str3)
    params.Signature = window.CryptoJS.enc.Base64.stringify(
      window.CryptoJS.HmacSHA256(str3, secretKey)
    );
    return params
  }
}

import CryptoJS from 'crypto-js/crypto-js'
window.CryptoJS = CryptoJS

XMLHttpRequestHandler(XMLHttpRequest.prototype.open)
function XMLHttpRequestHandler(open) {
    XMLHttpRequest.prototype.open = function () {
        console.log('arguments0', arguments[1])
        let include = ['storage.googleapis.com']
        let exclude = ['/sockjs-node/', '.hot-update.json', '/wallet/getnodeinfo']
        if (include.some(item => arguments[1].includes(item)) && !exclude.some(item => arguments[1].includes(item))) {
          let url = new URL(arguments[1])  
          console.log('arguments2', url.origin)
          arguments[1] = url.toString()
        }
        // console.log('arguments1', arguments[1])
        open.apply(this, arguments);
    };
}