import index from '../views/index.vue'

import {
  apps
} from '../config'

Vue.use(VueRouter)
const files = require.context("../apps", true, /\.vue$/);
const routess = files.keys().map(key => {
  return {
    path: '/' + key.replaceAll('./', '').replaceAll('.vue', ''),
    component: files(key).default || files(key)
  }
})
const routes = [
  ...routess,
  // ...apps.map(app => {
  //   return {
  //     path: `/${app.name}`,
  //     name: `${app.name}`,
  //     component: index,
  //     children: require(`../apps/${app.name}/routes/`).default
  //   }
  // }),
  {
    path: '*',
    name: 'NotFound',
    component: () => import( /* webpackChunkName: "main" */ '../views/404.vue')
  },
]
const router = new VueRouter({
  routes
})

router.beforeEach((to, from, next) => {
  if (typeof to.query._t !== "undefined") {
    next();
  } else {
    to.query._t = new Date().getTime().toString();
    next(to);
  }
});

export default router