<template>
  <div class="home">
    <div class="inputArea">
      <div class="item">
        <div class="title">币种</div>
        <el-input
          v-model="config.currencys"
          placeholder="请输入内容"
        ></el-input>
      </div>
      <div class="item">
        <div class="title">周期</div>
        <el-input v-model="config.period" placeholder="请输入内容"></el-input>
      </div>
      <div class="item">
        <div class="title">数量</div>
        <el-input v-model="config.size" placeholder="请输入内容"></el-input>
      </div>
    </div>
    <div class="buttons">
      <el-button type="primary" :loading="loading" @click="getData"
        >获取数据</el-button
      >
      <el-button type="primary" @click="testClick()">{{
        spotData.id || "获取账户ID"
      }}</el-button>
      <el-button type="primary" @click="testClick2()">测试2</el-button>
      <el-button type="danger" @click="$store.state.klineData = []"
        >删除网络数据</el-button
      >
    </div>
    <div class="data">
      <el-table
        :data="tableData"
        border
        style="width: 100%"
        v-loading="loading"
      >
        <el-table-column
          prop="currency"
          label="currency"
          width="80"
        ></el-table-column>
        <el-table-column
          prop="period"
          label="period"
          width="80"
        ></el-table-column>
        <el-table-column
          prop="price"
          label="price"
          width="80"
        ></el-table-column>
        <el-table-column
          prop="start"
          label="start"
          width="165"
        ></el-table-column>
        <el-table-column prop="end" label="end" width="165"> </el-table-column>
        <el-table-column prop="time" label="time" width="165">
        </el-table-column>

        <el-table-column prop="length" label="length"> </el-table-column>
        <el-table-column fixed="right" label="操作" width="80">
          <template slot-scope="scope">
            <el-button
              type="danger"
              size="small"
              @click="$store.state.klineData.splice(scope.$index, 1)"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-divider></el-divider>
    <div class="inputArea">
      <div class="item">
        <div class="title">默认U额</div>
        <el-input
          v-model="config.assetsAll"
          placeholder="请输入内容"
        ></el-input>
      </div>
      <div class="item">
        <div class="title">每笔交易额</div>
        <el-input
          v-model="config.assetsSplit"
          placeholder="请输入内容"
        ></el-input>
      </div>
      <div class="item">
        <div class="title">平衡值</div>
        <el-input
          v-model="config.assetsScale"
          placeholder="请输入内容"
        ></el-input>
      </div>

      <div class="item">
        <div class="title">交易条件</div>
        <el-input v-model="config.riseKey" placeholder="请输入内容"></el-input>
      </div>
      <div class="item">
        <div class="title">燃料费</div>
        <el-input v-model="config.gas" placeholder="请输入内容"></el-input>
      </div>
    </div>
    <div class="buttons">
      <el-button
        type="primary"
        :loading="loading2"
        @click="calculateDouble(klineData)"
        >计算</el-button
      >
      <el-button type="primary" @click="testClick(klineData)"
        >测试点击</el-button
      >
      <el-button type="danger" @click="$store.state.ccData = []"
        >删除计算数据</el-button
      >
    </div>
    <div class="data">
      <el-table :data="ccData" border style="width: 100%" v-loading="loading2">
        <el-table-column prop="time" label="time" width="165"></el-table-column>

        <el-table-column
          prop="currency"
          label="currency"
          width="100"
        ></el-table-column>
        <el-table-column prop="period" label="period"></el-table-column>
        <el-table-column prop="assetsAll" label="本金"> </el-table-column>

        <el-table-column prop="change" label="change" sortable>
        </el-table-column>
        <el-table-column prop="rate" label="rate" sortable> </el-table-column>

        <el-table-column prop="assetsSplit" label="assetsSplit">
        </el-table-column>
        <el-table-column prop="assetsScale" label="assetsScale">
        </el-table-column>

        <el-table-column prop="riseKey" label="riseKey"> </el-table-column>

        <el-table-column prop="assets" label="assets"> </el-table-column>
        <el-table-column prop="assets0" label="assets0"> </el-table-column>

        <el-table-column prop="assets1" label="assets1"> </el-table-column>
        <el-table-column prop="assetsNum0" label="assetsNum0">
        </el-table-column>

        <el-table-column prop="assetsNum1" label="assetsNum1">
        </el-table-column>

        <el-table-column prop="length" label="length"> </el-table-column>

        <el-table-column prop="buyCount" label="buyCount"> </el-table-column>

        <el-table-column prop="sellCount" label="sellCount"> </el-table-column>

        <el-table-column prop="buyError" label="buyError"> </el-table-column>

        <el-table-column prop="sellError" label="sellError"> </el-table-column>

        <el-table-column prop="gas" label="gas"> </el-table-column>

        <el-table-column fixed="right" label="操作" width="80">
          <template slot-scope="scope">
            <el-button
              type="danger"
              size="small"
              @click="$store.state.ccData.splice(scope.$index, 1)"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-divider></el-divider>
  </div>
</template>

<script>
// @ is an alias to /src
var axios = require("axios");
import HelloWorld from "@/components/HelloWorld.vue";
import { mapState, mapMutations, mapGetters } from "vuex";
export default {
  name: "Home",
  components: {
    HelloWorld,
  },
  data() {
    return {
      input: "",
      loading: false,
      loading2: false,
    };
  },
  computed: {
    ...mapState({
      config: (state) => state.config,
      urls: (state) => state.urls,
      klineData: (state) => state.klineData,
      calculateData: (state) => state.calculateData,
      ccData: (state) => state.ccData,
      spotData: (state) =>
        state.accounts.filter((item) => item.type == "spot")[0] || {},
    }),
    currencys() {
      return this.config.currencys.split(",");
    },
    periods() {
      return this.config.period.split(",");
    },
    tableData() {
      return this.klineData.map((item) => {
        return {
          currency: item.currency,
          period: item.period,
          price: item.price,
          length: item.list.length,
          start: _D(item.list[0].id * 1000),
          end: _D(item.list[item.list.length - 1].id * 1000),
          time: _D(),
        };
      });
    },
  },
  methods: {
    testClick(klineData) {
      if (klineData.length == 2) {
        let list0 = klineData[0].list;
        let list1 = klineData[1].list;
        for (const key in list0) {
          if (list0[key].id == list1[key].id) {
          } else {
            this.$message.error("数据对时间异常" + key);
            return;
          }
        }
        this.loading2 = true;
        let data = [
          this.config.assetsAll,
          this.config.assetsSplit,
          this.config.assetsScale,
          this.config.riseKey,
          this.config.gas,
        ].map((item) => {
          let list = [];
          let arr = String(item).split("-");
          if (arr.length > 1) {
            let arr2 = arr[1].split(".");
            let gap = 1;
            if (arr2.length > 1) {
              gap = Math.pow(10, -arr2[1].length);
            }
            let start = parseFloat(arr[0]);
            let newNum = start;
            list.push(start);
            while (parseFloat(list[list.length - 1]) < parseFloat(arr[1])) {
              newNum = _Round(
                list[list.length - 1] + gap,
                gap == 1 ? 0 : arr2[1].length
              );
              list.push(newNum);
            }
          } else {
            list = arr;
          }
          return list;
        });

        var worker = new Worker("./work.js");
        worker.postMessage({
          data,
          klineData,
        });
        worker.onmessage = (event) => {
          // console.log("Received message " + event.data);
          this.loading2 = false;

          let arr = event.data;

          Log("计算完成", arr.length);
          this.$store.state.ccData = arr;
          worker.terminate();
        };
      } else {
        this.$message.error("请选择一对数据");
      }
    },
    testClick2() {},
    handleClick(scope, type) {
      if (type == 1) {
        this.$store.state.calculateData = [
          ...this.calculate1([this.klineData[scope.$index]]),
          ...this.$store.state.calculateData,
        ];
      } else if (type == 2) {
        this.$store.state.calculateData.splice(
          scope.$index,
          1,
          this.calculate1([this.calculateData[scope.$index].item])[0]
        );
      }
    },
    getData() {
      let currencys = this.currencys;
      let periods = this.periods;
      let arr = [];
      let klineData = [];
      currencys.forEach((currency) => {
        periods.forEach((period) => {
          arr.push(
            GetRecords(currency, period, this.config, this.urls.klineUrl)
          );
          klineData.push({
            currency,
            period,
            list: [],
            price: 0,
          });
        });
      });
      console.log(currencys);
      console.log(periods);
      this.loading = true;
      Promise.all(arr)
        .then((result) => {
          this.loading = false;
          result.forEach((element, index) => {
            klineData[index].list = [...element].reverse();
            klineData[index].price = klineData[index].list[0].close;
          });
          this.$store.state.klineData = [
            ...klineData,
            ...this.$store.state.klineData,
          ];
        })
        .catch((error) => {
          console.log(error);
        });
    },
    async calculateDouble(klineData) {
      if (klineData.length == 2) {
        let list0 = klineData[0].list;
        let list1 = klineData[1].list;
        for (const key in list0) {
          if (list0[key].id == list1[key].id) {
          } else {
            this.$message.error("数据对时间异常" + key);
            return;
          }
        }
        this.loading2 = true;

        let data = [
          this.config.assetsAll,
          this.config.assetsSplit,
          this.config.assetsScale,
          this.config.riseKey,
          this.config.gas,
        ].map((item) => {
          let list = [];
          let arr = String(item).split("-");
          if (arr.length > 1) {
            let arr2 = arr[1].split(".");
            let gap = 1;
            if (arr2.length > 1) {
              gap = Math.pow(10, -arr2[1].length);
            }
            let start = parseFloat(arr[0]);
            let newNum = start;
            list.push(start);
            while (parseFloat(list[list.length - 1]) < parseFloat(arr[1])) {
              newNum = _Round(
                list[list.length - 1] + gap,
                gap == 1 ? 0 : arr2[1].length
              );
              list.push(newNum);
            }
          } else {
            list = arr;
          }
          return list;
        });

        let list = [];
        data[0].forEach((item0) => {
          data[1].forEach((item1) => {
            data[2].forEach((item2) => {
              data[3].forEach((item3) => {
                data[4].forEach((item4) => {
                  list.push([item0, item1, item2, item3, item4]);
                });
              });
            });
          });
        });
        let arr = [];
        list.forEach((item) => {
          arr.push(
            this.calculateDouble2(
              klineData,
              item[0],
              item[1],
              item[2],
              item[3],
              item[4]
            )
          );
        });
        Log("计算完成", arr.length);
        this.loading2 = false;
        this.$store.state.ccData = arr;
      } else {
        this.$message.error("请选择一对数据");
      }
    },
    calculateDouble2(
      klineData,
      assetsAll = 100,
      assetsSplit = 5,
      assetsScale = 1.2,
      riseKey = 0.005,
      gas = 0.002
    ) {
      assetsSplit = parseFloat(assetsSplit);
      assetsScale = parseFloat(assetsScale);
      riseKey = parseFloat(riseKey);
      gas = parseFloat(gas);

      let info = {
        buyCount: 0,
        sellCount: 0,
        buyError: 0,
        sellError: 0,
        assets: parseFloat(assetsAll),
        assetsNum0: assetsAll / klineData[0].list[0].close,
        assetsNum1: assetsAll / klineData[1].list[0].close,
      };

      klineData[0].list.forEach((item, index) => {
        let item0 = klineData[0].list[index];
        let item1 = klineData[1].list[index];
        this.calculateDouble3(
          item0,
          "assetsNum0",
          info,
          assetsAll,
          assetsSplit,
          assetsScale,
          riseKey,
          gas
        );
        this.calculateDouble3(
          item1,
          "assetsNum1",
          info,
          assetsAll,
          assetsSplit,
          assetsScale,
          riseKey,
          gas
        );
      });
      let data = {
        currency: klineData[0].currency + "," + klineData[1].currency,
        period: klineData[0].period,
        length: klineData[0].list.length,
        assets0: _N(
          info.assetsNum0 *
            klineData[0].list[klineData[0].list.length - 1].close,
          2
        ),
        assets1: _N(
          info.assetsNum1 *
            klineData[1].list[klineData[1].list.length - 1].close,
          2
        ),
        assetsAll,
        assetsSplit,
        assetsScale,
        riseKey,
        gas,
        ...info,
        time: _D(),
        assets: _N(info.assets, 2),
      };
      data.change = _N(
        data.assets + data.assets0 + data.assets1 - 3 * assetsAll,
        2
      );
      data.rate = data.change / (parseFloat(assetsAll) * 3);
      return data;
    },
    calculateDouble3(
      item,
      assetsNumkey,
      info,
      assetsAll,
      assetsSplit,
      assetsScale,
      riseKey,
      gas
    ) {
      let assetsSplitMax = assetsSplit * assetsScale;

      let balance2usdt = info[assetsNumkey] * item.close;

      let rise = (item.close - item.open) / item.open;
      if (Math.abs(rise) > parseFloat(riseKey)) {
        if (rise > 0) {
          // 卖出
          let assetsSplitS =
            balance2usdt > info.assets ? assetsSplitMax : assetsSplit;
          let num = parseFloat(assetsSplitS) / item.close;
          if (info[assetsNumkey] > num) {
            info.assets += parseFloat(assetsSplitS) * (1 - parseFloat(gas));
            info[assetsNumkey] -= parseFloat(assetsSplitS) / item.close;
            info.sellCount++;
          } else {
            info.sellError++;
            // Log('币不足最小交易额度')
          }
        } else {
          // 买入
          let assetsSplitS =
            balance2usdt > info.assets ? assetsSplit : assetsSplitMax;
          if (info.assets > assetsSplitS) {
            info.assets -= assetsSplitS;
            info[assetsNumkey] +=
              (assetsSplitS / item.close) * (1 - parseFloat(gas));
            info.buyCount++;
          } else {
            info.buyError++;
            // Log('资金不足最小交易额度')
          }
        }
      }
    },
  },
};

function GetRecords(currency, period, config, url) {
  let size = config.size;
  let params = {
    symbol: (currency + "usdt").toLowerCase(),
    period,
    size,
  };
  return new Promise((reslove, reject) => {
    axios({
      method: "get",
      url,
      params,
    })
      .then((res) => {
        let records = res.data.data;
        reslove(records);
      })
      .catch((res) => {
        Log("错误:GetRecords", res);
        reslove([]);
      });
  });
}
function _D(time) {
  return require("dayjs")(time).format("YYYY-MM-DD HH:mm:ss");
}

function _N(time, num = 0) {
  return Math.floor(time * Math.pow(10, num)) / Math.pow(10, num);
}

function _Round(time, num = 0) {
  return Math.round(time * Math.pow(10, num)) / Math.pow(10, num);
}

function Log() {
  console.log(_D(), ...arguments);
}
</script>


