<template>
  <div class="scroll" ref="scroll" @scroll="scroll" @touchend="touchend">
    <video
      class="video"
      controls="controls"
      ref="video"
      playsinline
      autoplay
      :src="currentItem && currentItem.videoUrl"
      :poster="currentItem && currentItem.vodrow.coverpic"
    ></video>
    <div
      class="swiperItem"
      v-for="(item, index) in rows"
      :key="index"
      @click="clickHandler(item, index)"
      :ref="'index' + index"
    >
      <div class="itemContent" v-if="Math.abs(index - current) < 3">
        <div class="content" :ref="'content' + index">
          <img :src="item.vodrow.coverpic" :style="current == index ? 'filter: blur(20px);' : ''">
        </div>
        <div class="mask" v-if="isShowMask" @click="tipClick">
          <div>@{{item.user.nickname}}</div>
          <div>{{item.vodrow.title}}</div>
          <div>{{item.vodrow.updatetime}}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import config from "../js/config.js";
export default {
  data() {
    return {
      rows: [],
      current: 0,
      isShowMask: true,
      options: {
        scrollY: true
      },
      isVideoLoading: false,
      isMoreLoading: false
    };
  },
  computed: {
    currentItem() {
      return this.rows.length > 0 ? this.rows[this.current] : null;
    }
  },
  created() {
    console.log("created banana");
    this.loadData(true);
  },
  mounted() {
    //   document.body.appendChild(this.$refs.swiperContainer)
  },
  methods: {
    scroll(e) {
      // console.log("scroll", e);
      let scrollTop = this.$refs.scroll.scrollTop;
      let clientHeight = this.$refs.scroll.clientHeight;
      let current = Math.round(scrollTop / clientHeight);
      this.changePage(current);
    },
    touchend(e) {
      return;
      let scrollTop = this.$refs.scroll.scrollTop;
      let clientHeight = this.$refs.scroll.clientHeight;
      let current = Math.round(scrollTop / clientHeight);
      this.current = current;
      console.log("touchend", scrollTop, current);
      let item = this.$refs["index" + current][0];
      item.scrollIntoView({ behavior: "smooth" });
      setTimeout(() => {
        item.scrollIntoView({ behavior: "smooth" });
      }, 1000);
    },
    loadData(isFirst) {
      this.isMoreLoading = true
      this.$axios
        .request({
          url: config.origin + "/minivod/reqlist",
          params: config.query()
        })
        .then(res => {
          this.isMoreLoading = false
          console.log("success", res);
          if (res.status == 200 && res.data.retcode == 0) {
            var rows = [];
            res.data.data.rows.map(item => {
              if (item.vodrow && item.vodrow.vodid) {
                item.videoUrl = "";
                rows.push(item);
              }
            });
            if (isFirst) {
              this.rows = rows;
              this.changePage(0);
            } else {
              this.rows.push(rows[0]);
            }
          } else {
            weui.topTips(res.data.errmsg || res.statusCode || "fail");
          }
        })
        .catch(res => {
          this.isMoreLoading = false
          weui.topTips(res.message || "fail");
        });
    },
    clickHandler(item, index) {
      this.isShowMask = !this.isShowMask;
    },
    tipClick(e) {
      this.$refs.video.play();
      this.isShowMask = !this.isShowMask;
      e.stopPropagation();
    },
    changePage(current) {
      const item = this.rows[current];
      const vodid = item.vodrow.vodid;
      // console.log("swiperChange", current, item);
      if (document) {
        document.title = item.vodrow.title;
      }
      this.current = current;
      if (!item.videoUrl && !this.isVideoLoading) {
        this.isVideoLoading = true;
        this.$axios
          .request({
            url: config.origin + "/minivod/reqplay/" + vodid,
            params: config.query()
          })
          .then(res => {
            this.isVideoLoading = false;
            console.log("success", res);
            var videoUrl =
              res.data.data.httpurl || res.data.data.httpurl_preview;
            if (videoUrl) {
              console.log(videoUrl);
              this.rows[current].videoUrl = videoUrl;
              setTimeout(() => {
                this.$refs["content" + this.current][0].appendChild(
                  this.$refs.video
                );
                this.$refs.video.play();
              }, 20);
            } else {
              weui.topTips(res.data.errmsg || res.statusCode || "fail");
            }
          })
          .catch(res => {
            this.isVideoLoading = false;
            weui.topTips(res.message || "fail");
          });
      } else if (
        !this.$refs["content" + this.current][0].querySelector(".video")
      ) {
        this.$refs["content" + this.current][0].appendChild(this.$refs.video);
      }
      if (current == this.rows.length - 1 && !this.isMoreLoading) {
        this.loadData();
      }
    }
  },
  watch: {}
};
</script>

