        font-size: 13px;
        z-index: 100;
        background-color: rgba(0, 0, 0, 0.5);
        opacity: 0.8;
        display: inline;
        // transform: translateX(-50%);
        // left: 50%;
        // text-align: center;
      }
      .content {
        display: flex;
        align-items: center;
        height: 100%;
        img {
